.featured-blog-banner-main-container {
  /* height: calc(90vh - 80px); */
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  /* align-items: center; */
  flex-wrap: wrap;
  /* margin-top: 5vh; */
  padding: 0 75px;
}

.featured-posts {
  width: 100%;
  height: fit-content;
  margin-top: 20px;
  /* padding: 0px 60px 0 60px; */
}

.featured-posts h1 {
  margin: 0;
}

.featured-posts-line {
  flex-grow: 1;
  border-bottom: 2px solid black;
}

.featured-blog-banner-inner-container {
  /* width: 90vw; */
  width: 100%;
  height: 70vh;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 5px;
  margin-top: -75px;
}

.featured-blog-banner-inner-container-first {
  flex: 3;
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692792125/Blogs/Featured-Blog-Banner/feature-1_gxjyxp.webp");
  background-size: 100% 100%;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  transition: all 0.3s ease-in;
  position: relative;
}

.read-more-div {
  position: absolute;
  bottom: 30px;
  left: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #fff;
}

.featured-blog-banner-inner-container-first h2 {
  font-size: larger;
  max-width: 100%;
}

.featured-blog-banner-inner-container-first h4 {
  position: absolute;
  bottom: 10px;
  left: 20px;
  color: #fff;
  font-size: medium;
}

.comments-blogs {
  position: absolute;
  bottom: 10px;
  right: 20px;
}

.featured-blog-banner-inner-container-first:hover {
  transform: scale(1.05);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.75);
  cursor: pointer;
  z-index: 2;
}

.featured-blog-banner-inner-container-first p {
  color: #fff;
  font-weight: bold;
  font-size: large;
  position: absolute;
  bottom: 35px;
  right: 20px;
}

.featured-blog-banner-inner-container-second {
  flex: 2;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 5px;
}

.featured-blog-banner-inner-container-second-first-blog {
  flex: 1;
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692792125/Blogs/Featured-Blog-Banner/feature-2_g4itzh.webp");
  background-position: center;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  transition: all 0.3s ease-in;
  position: relative;
}

.featured-blog-banner-inner-container-second-first-blog .read-more-div {
  position: absolute;
  bottom: 30px;
  left: 20px;
  font-size: small;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #fff;
}

.featured-blog-banner-inner-container-second-first-blog h2 {
  font-size: large;
}

.featured-blog-banner-inner-container-second-first-blog h4 {
  position: absolute;
  font-size: medium;
  bottom: 10px;
  left: 20px;
  color: #fff;
}

.featured-blog-banner-inner-container-second-first-blog p {
  color: #fff;
  font-weight: bold;
  font-size: large;
  position: absolute;
  bottom: 35px;
  right: 15px;
}

.featured-blog-banner-inner-container-second-first-blog:hover {
  transform: scale(1.05);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.75);
  cursor: pointer;
}

.featured-blog-banner-inner-container-second-second-blog {
  flex: 1;
  display: flex;
  flex-direction: row;
  gap: 5px;
}

.featured-blog-banner-inner-container-second-second-blog-one {
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692792133/Blogs/Featured-Blog-Banner/Book1_avjadw.webp");
  background-position: center;
  background-size: 100% 100%;
  flex: 1;
  transition: all 0.3s ease-in;
  position: relative;
}

.featured-blog-banner-inner-container-second-second-blog-one:hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.75);
  cursor: pointer;
}

.featured-blog-banner-inner-container-second-second-blog-one .read-more-div {
  position: absolute;
  bottom: 20px;
  left: 20px;
  font-size: small;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #fff;
}
.featured-blog-banner-inner-container-second-second-blog-one h4 {
  position: absolute;
  font-size: small;
  bottom: 10px;
  left: 20px;
  color: #fff;
}

.featured-blog-banner-inner-container-second-second-blog-one p {
  color: #fff;
  font-size: large;
  position: absolute;
  bottom: 25px;
  right: 15px;
}

.featured-blog-banner-inner-container-second-second-blog-two {
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692792133/Blogs/Featured-Blog-Banner/Book2_iqjdg8.webp");
  background-position: center;
  background-size: 100% 100%;
  flex: 1;
  transition: all 0.3s ease-in;
  position: relative;
}

.featured-blog-banner-inner-container-second-second-blog-two:hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.75);
  cursor: pointer;
}

.featured-blog-banner-inner-container-second-second-blog-two .read-more-div {
  position: absolute;
  bottom: 20px;
  left: 20px;
  font-size: small;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #fff;
}

.featured-blog-banner-inner-container-second-second-blog-two h4 {
  position: absolute;
  font-size: small;
  bottom: 10px;
  left: 20px;
  color: #fff;
}

.featured-blog-banner-inner-container-second-second-blog-two p {
  color: #fff;
  font-size: large;
  position: absolute;
  bottom: 25px;
  right: 15px;
}

@media (max-width: 1024px) {
  .comments-blogs {
    display: none;
  }

  .featured-blog-banner-inner-container-first p {
    display: none;
  }

  .featured-blog-banner-inner-container-second p {
    display: none;
  }
}

@media (max-width: 768px) {
  .featured-blog-banner-main-container {
    padding: 10px 50px;
  }

  .featured-blog-banner-inner-container-first {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.75);
  }

  .featured-blog-banner-inner-container-first:hover {
    /* Reset hover styles for tablets */
    transform: none;
    /* box-shadow: none; */
    cursor: default;
    z-index: auto;
  }

  .featured-blog-banner-inner-container-second-first-blog {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.75);
  }

  .featured-blog-banner-inner-container-second-first-blog:hover {
    transform: none;
    box-shadow: none;
    cursor: pointer;
  }

  .featured-blog-banner-inner-container-first .read-more-div {
    width: 100%;
  }


  .featured-blog-banner-inner-container-first h2 {
    font-size: large
  }

  .featured-blog-banner-inner-container-second-first-blog h2 {
    font-size: large;
  }

  .featured-blog-banner-inner-container-second-first-blog .read-more-div {
    bottom: 30px;
    left: 10px;
  }

  .featured-blog-banner-inner-container-second-second-blog-one h2 {
    font-size: 0.8rem;
  }

  .featured-blog-banner-inner-container-second-second-blog-two h2 {
    font-size: 0.8rem;
  }

  .featured-blog-banner-main-container h4 {
    display: none;
  }

  .read-more-div {
    bottom: 20px;
  }

  .featured-blog-banner-inner-container-second-second-blog-two h4 {
    display: none;
  }

  .featured-posts {
    margin-top: 0px;
    /* padding: 0px 50px 0 50px; */
  }
}

@media (max-width: 425px) {
  .featured-blog-banner-main-container {
    align-items: normal;
    padding: 15px 20px;

    /*  */
    height: 150vh;
  }

  .featured-blog-banner-inner-container {
    flex-direction: column;
    margin-top: 0px;
  }

  .featured-blog-banner-inner-container-first {
    max-height: 50vh;
    max-width: 100vw;
  }

  .featured-blog-banner-inner-container-first .read-more-div {
    width: 100%;
    font-size: medium;
  }

  .featured-blog-banner-inner-container-first .read-more-div h2{
    font-size: larger;
  }

  .featured-blog-banner-inner-container-second-first-blog .read-more-div {
    position: absolute;
    bottom: 30px;
    left: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: medium;
  }

  .featured-blog-banner-inner-container-second-first-blog .read-more-div h2{

    font-size: larger;
  }

  .featured-blog-banner-inner-container-second-first-blog {
    max-height: 50vh;
    max-width: 100vw;
  }

  .featured-blog-banner-inner-container-second-second-blog {
    max-height: 50vh;
    max-width: 100vw;
  }
  .featured-blog-banner-inner-container {
    height: 150vh;
  }

  .featured-blog-banner-main-container .featured-posts {
    margin-top: 0;
    margin-bottom: 20px;
    /* padding: 0px 20px 0 20px; */
  }
}
